import * as common from './common.js';
import * as db from './db.js'
import {
	apiBaseUrl
} from './config.js'

// 
const methodsToken = [
	'userInfoInit',
	'userInfoById',
	'addCouponByUser',
	'getbeginMoneyUser',
	'userRecharge',
	'getCouponByUser',
	'getAddressList',
	'AddaddressUser',
	'getAddressBydefault',
	'getGoodsInfoCardById',
	'shopping',
	'getOrderListcompleted',
	'isFlashGoods'
];

const post = (url, data, callback) => {
	uni.showLoading({
		title: ''
	});

	// token
	if (methodsToken.indexOf(url) >= 0) {
		// token
		let userToken = db.get("userToken");
		if (!userToken) {
			common.jumpToLogin();
			return false;
		} else {
			data.userToken = userToken;
		}
	}

	uni.request({
		url: apiBaseUrl + url,
		data: data,
		header: {
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			// 'Content-Type': 'application/x-www-form-urlencoded', //
		},
		method: 'POST',
		success: (response) => {
			uni.hideLoading();
			const result = response.data;
			callback(result);
		},
		complete: () => {
			uni.hideLoading();
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
	});

}

const get = (url, callback) => {
	uni.showLoading({
		title: ''
	});
	uni.request({
		url: apiBaseUrl + url,
		header: {
			'Accept': 'application/json',
			'Content-Type': 'application/x-www-form-urlencoded', //
		},
		method: 'GET',
		success: (response) => {
			uni.hideLoading();
			callback(response.data);
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
		complete: () => {
			setTimeout(function() {
				uni.hideLoading();
			}, 250);
		}
	});
}

